import React, { useState,Component, useReducer } from 'react'
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Link
} from "react-router-dom";
import Signup from './components/Signup';
import Login from './components/Login';
import Profile from './components/Profile';
import Forgetpassword from './components/Forgetpassword';
import Confirmpassword from './components/Confirmpassword';
const initialState = {count: "none"};

function reducer(state, action) {
  switch (action.type) {
    case 'display':
      return {count: true};
    case 'none':
      return {count: "none"};
    default:
      throw new Error();
  }
}

function App() {
const [state, dispatch] = useReducer(reducer, initialState);
  return (
    <Router>
      <Routes>
        <Route exact path="/register"  element={<Signup/>} />
        <Route exact path="/profile/*" element={<Profile profileDisplay={state.count} dispatch={dispatch} />} />
        <Route exact path="/forgetpassword"  element={<Forgetpassword />} />
        <Route exact path="/confirmpassword/*"  element={<Confirmpassword />} />
        <Route exact path="/"  element={<Login />} />
      </Routes>
  </Router>
  )
}

export default App
