import React, {useState, useEffect} from 'react';
import {useNavigate } from "react-router-dom";
import Profile from './Profile';
function Editsection({user,dispatch, ...props}) {
  console.log(props)
    console.log(user)
    console.log(window.location.href)
    const navigate = useNavigate();
    const [firstName, setFirstName] = useState("");
    const [email, setEmail] = useState("");
    const [lastName, setLastName] = useState("");
    const [avatar, setAvatar] = useState("");
    const [profilePic, setProfilePic] = useState("")
    useEffect(() => {
      document.getElementById("onlineUsers").style.display = "none";
     }, [])
    useEffect(() => {
     setFirstName(user.firstName); 
    }, [user])
    useEffect(() => {
      setLastName(user.lastName); 
     }, [user])
     useEffect(() => {
      setEmail(user.email); 
     }, [user])
    const onImageChange = (event) => {
      setProfilePic(event.target.files[0])
      console.log(event.target.files[0])
        if (event.target.files && event.target.files[0]) {
    
          let reader = new FileReader();
          reader.onload = (e) => {
            setAvatar( e.target.result);
          };
          reader.readAsDataURL(event.target.files[0]);
        }
      }
    function cancel() {
      dispatch({type: "display"})
      navigate("/profile");
    }
    function save() {
          console.log(profilePic)
          fetch('/api/editprofile',{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({firstName, lastName, email})
        }).then(r => r.json()).then(data => { 
          if(data.msg === "success") {
            //navigate("/profile");
          } else {
            alert(data.msg)
          }
        }).then(() => {
          if(profilePic === "") {
            dispatch({type: "display"})
             navigate("/profile")
            
            //window.location.href = "/profile";
            //ocument.getElementById("profile").style.display = true;
            return;
          }
        const formData  = new FormData();
        formData.append("picture", profilePic);
        fetch('/api/editprofileimage',{method:"POST",
        body: formData
      }).then(r => r.json()).then(data => { 
        if(data.msg === "success") {
          setProfilePic("")
          dispatch({type: "display"})
          navigate("/profile");
          //document.getElementById("profile").style.display = true;
          //window.location.href = "/profile";
          
        } else {
          alert(data.msg)
        }
      })
    })

    }
    return (
            <div className="profile__section" >           
                <img src={ avatar || user.photo  || "/avatar.png"} className="profile__picture" alt="profile" />
                <small style={{color:"red",display: profilePic.size>2097152 ? true: "none" }}>image max size is 2 megabyte</small>
                <input type="file" onChange={(e) => onImageChange(e) }/>
                <label htmlFor="firstName">First Name</label>
                <input type="text" name="firstName" maxLength="256" value={firstName || ""} onChange={(e) => setFirstName(e.target.value) }/>
                <small style={{color:"red",display:!firstName?true:"none" }}>Field required</small>
                <label htmlFor="lastName">Last Name</label>
                <input type="text" name="lastName" maxLength="256" value={lastName || ""} onChange={(e) => setLastName(e.target.value) }/>
                <small style={{color:"red",display:!lastName?true:"none" }}>Field required</small>
                <label htmlFor="email">Email</label>
                <input type="email" name="email" maxLength="256" value={email || ""} onChange={(e) => setEmail(e.target.value) }/>
                <small style={{color:"red",display:(!email || email.indexOf("@")===-1 || email.indexOf(".")===-1) ?true:"none" }}>Invalid Email</small>
                <button className="logout__button" onClick={cancel} >Cancel</button>
                <button className="edit__button" style={{backgroundColor:!firstName || profilePic.size>2097152 || !lastName ||email.indexOf("@") === -1 || email.indexOf(".") === -1 ?"red": null}} disabled={!firstName || profilePic.size>2097152 || !lastName? true:false} onClick={save} >Save</button>
            </div> 
    )
}

export default Editsection
