import React,{useEffect, useState,} from 'react'
import {Helmet} from 'react-helmet';
import Editsection from './Editsection';
import {
    Routes,
    Route,
    useNavigate,
  } from "react-router-dom";
  import { io } from "socket.io-client";
  const URL = "harut-app.herokuapp.com"//"localhost:5000"
  const socket = io(URL);
function Profile({dispatch,profileDisplay, ...props}) {
    // let { path, url } = useParams();
    // const inputEl = useRef(null);
    // console.log(url, path,inputEl);
    const navigate = useNavigate();
    const [showProfileSection, setShowProfileSection] = useState(true);
    const [sendMessage, setSendMessage] = useState("");
    const [Messages, setMessages] = useState([]);
    const [activeUsers, setActiveUsers] = useState([]);
    const [chooseUser, setChooseUser] = useState("");
    const [user, setUser] = useState({});
    console.log(profileDisplay)
    useEffect(() => {
        async function fetchMyAPI() {
            const response = await fetch('/api/profile', { method: 'GET', mode: 'cors',headers: {
                'Content-Type': 'application/json'
            },})
            const data = await response.json();
            data.msg !== "/"? setUser(data): navigate("/")
            
        }
        fetchMyAPI()
    },[profileDisplay])
    useEffect(() => {
       socket.emit("addUser",user.id, user.firstName, user.photo)
    },[user])
    useEffect(() => {
        
        socket.on("hello", (data) => {
            console.log(data)
            if(data !== "connected") {
                window.scrollTo(0,document.body.scrollHeight);
                setMessages(oldArray =>[...oldArray,data])
            }
        });
    },[])
    useEffect(() => {
        
        socket.on("getUsers", (data) => {
            console.log(data)
            //if(JSON.stringify(data) !== JSON.stringify(activeUsers)) {
                 setActiveUsers(data)
            //}
            
        });
    },[])
    useEffect(() => {
        
        socket.on("getNotification", (data) => {
            console.log("notification", data)
            const snd = new Audio("mixkit-software-interface-start-2574.wav"); // buffers automatically when created
            snd.play();
            if(document.querySelector(`[data-id="${data.userId}"]`).style.backgroundColor !== "green") {
                document.querySelector(`[data-id="${data.userId}"]`).children[0].children[2].style.display = "inline";
            }
            
        });
    },[])
    // useEffect(() => {
    //     console.log(showProfileSection)
    //     console.log(window.location.pathname);
    //     if (window.location.pathname === "/profile") {
    //         setShowProfileSection(true);
    //     }  
    //     if (window.location.pathname === "/profile/edit") {
    //         setShowProfileSection("none");
    //     } 
    // },)
    function edit() {
        dispatch({type: "none"})
        setShowProfileSection("none");
        navigate(`/profile/edit`);
    }
    function logOut() {
        socket.emit("logout")
        fetch('/logout',{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
        }).then(r => navigate("/"))
    }
    async function  send() {
        console.log(sendMessage)
        const response = await fetch('/api/sendmessage',{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({name: user.firstName, receiverId: chooseUser, data: sendMessage})
        })
        console.log(response)
        socket.emit("hello", sendMessage, chooseUser, user.firstName, user.id);
        window.scrollTo(0,document.body.scrollHeight);
        document.querySelector("textarea").value = "";
        setSendMessage("");
        
    }
     function userChoose(e) {
        
        [...document.getElementById("onlineUsers").children[2].children].map(item => item.style.backgroundColor = "inherit")
        console.log(e.target, e.currentTarget)
        e.currentTarget.style.backgroundColor = "green";
        e.currentTarget.children[0].children[2].style.display = "none";
        fetch('/api/getmessage',{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({receiverId: Number(e.target.parentNode.getAttribute("data-id")) })
        }).then(r =>  r.json()).then(data => setMessages(data)).then(() => window.scrollTo(0,document.body.scrollHeight))
        
        setChooseUser(Number(e.target.parentNode.getAttribute("data-id")));

    }
    function goToSettings() {
        dispatch({type: "display"})
    }
    function goToOnlineUsers() {
        dispatch({type: "none"})
    }

    return (
        <div className="main">
            <Helmet>
                <link rel="stylesheet" href="/component_css/profile.css" />
            </Helmet>
            <Routes>
                <Route exact path={`/edit`} element={<Editsection dispatch={dispatch}   user={user} />} />
            </Routes>
            <div id="profile" style={{display:profileDisplay}} className="profile__section" >  
                <i id="onlineUsersicon" onClick={() => { goToOnlineUsers()}} className="fas fa-arrow-circle-left"></i>         
                <img src={user.photo === null?"/avatar.png":user.photo } className="profile__picture" alt="profile" />
                <div className="firstname__section">
                    <p>{user.firstName} {user.lastName}</p>
                </div>
               
                <div className="email__section">
                    <p>{user.email}</p>
                </div>
                <button className="logout__button" onClick={logOut} >Logout</button>
                <button className="edit__button" onClick={() => { edit()}} >Edit</button>
                
            </div> 
            <div id="onlineUsers" style={{display:profileDisplay==="none"? true:"none"}}className="profile__section">
                <div onClick={() => { goToSettings()}} id="settingsIcon">
                    <i  className="fas fa-bars"></i>
                </div>
            
                <h2 >Online</h2>
                <ul>
                    {
                        activeUsers && activeUsers.map((item, index) => {
                            if(item.userId !== user.id){
                                return (
                                <li key={index} data-id={item.userId} onClick={(e) => { userChoose(e)}}><div className="active__user" data-id={item.userId}><img  width={"50px"} src={item.photo === null?"/avatar.png":item.photo } className="profile__picture" alt="profile" /><p>{item.name}</p><i style={{display: "none"}} className="fa fa-comment" aria-hidden="true"></i></div></li>
                                )
                            }
                        })
                    }
                </ul>
            </div>
            <div className="posts__section">

                
                <ul className="message__section">
                    {
                        Messages.map((item,index) => {
                            if(chooseUser === item.receiverId || item.userId === chooseUser) {
                                return (
                                    <li style={{left:item.userId === user.id?"100px":null}}  key={index}><p style={{background: item.userId === user.id? "white": "linear-gradient(#9ed387, #77ba5a)" }}>{item.data}</p></li>
                                )
                            }
                        })
                    }
                </ul>
                
            </div>   
            <div className='textarea__section'>
                    <textarea onChange={(e) => setSendMessage(e.target.value)}></textarea>
                    <button className="edit__button"  onClick={() => { send()}} ><i className="fas fa-paper-plane"></i></button>
            </div>
        </div>
    )
}

export default Profile
